.body {
  font-family: Lato, sans-serif;
}

h2 {
  margin: 0;
  font-size: 50px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 10px solid rgba(0, 0, 0, .15);
  border-color: rgba(0, 0, 0, .15) rgba(0, 0, 0, .25) rgba(0, 0, 0, .35) rgba(0, 0, 0, .5);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: block;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  max-width: 1400px;
  border: 2px solid rgba(0, 0, 0, .3);
  border-radius: 8px;
  gap: 10px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  display: flex;
}

@media only screen and (max-width: 768px) {
  .cat-info {
    flex-direction: column;
  }
}

.cat-img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  flex-shrink: 0;
  display: block;
}

@media only screen and (min-width: 768px) {
  .cat-img {
    height: 500px;
  }
}

.cat-text-bold {
  font-weight: 700;
}

.hidden {
  display: none;
}

